<template>
  <section>
    <div
      v-if="!parsed"
      class="bmt__import"
    >
      <textarea
        class="bmt__import__textarea"
        wrap="physical"
        v-model="team"
        placeholder="Paste the exported text from Pokemon Showdown here"
      ></textarea>
      <div>
        Select a tier:
        <select v-model="selectedTier">
          <option
            disabled
            value=""
          >Please select one</option>
          <option
            v-for="(value, key) in tiers"
            :key="key"
          >
            {{key}}
          </option>
        </select>
      </div>
      <button @click="parseIt()">parse it</button>
    </div>
    <TeamView
      v-else
      :parsed='parsed'
    />
  </section>
</template>


<script>
import axios from "axios";
import TeamView from "@/components/TeamView.vue";
const parseTeam = () => import("@/services/teamParser");

export default {
  title: 'Break My Team',
  components: {
    TeamView: TeamView
  },
  data() {
    return {
      team: "",
      parsed: false,
      selectedTier: false,
      cnc: {},
      tiers:{
        'ou': 100,
        'vgc': 50,
        'bss': 50,
        'ru': 100,
        'nu': 100,
        'ubers': 100,
        'monotype': 100,
        'uu': 100
      }
    };
  },
  methods: {
    parseIt() {
      if (this.selectedTier !== false) {
        parseTeam().then(res => {
          let parsed = {};
          parsed.pokemon = res.parseTeam(this.team);

          for (let index in parsed.pokemon) {
            if (parsed.pokemon[index].level === undefined) parsed.pokemon[index].level = this.tiers[this.selectedTier]
          }
          parsed.raw = this.team
          parsed.tier = {defaultLevel: this.tiers[this.selectedTier], alias: this.selectedTier};
          
          axios.post("/breakmyteam", parsed).then(res => {
            if (res.data.sucess) {
              this.parsed = res.data.response;
            } else {
              alert(res.data.message);
            }
          });
        });
      }
    }
  }
};
</script>